.todo-item{
  padding: 1rem;
  display: flex;
  align-items: center; /* 세로 가운데 정렬 */
  cursor: pointer;
  transition: all 0.15s;
  user-select: none;
}
.todo-item:hover {background: #e3fafc;}
.todo-item:hover .remove {opacity: 1;}
/* todo-item 사이에 윗 테두리 */
.todo-item + .todo-item {
  border-top: 1px solid #f1f3f5;
}
.remove{
  margin-right : 1rem; color: #e64980;
  font-weight:bold;
  opacity: 0;
}
.todo-text{
  flex:1; /* 체크박스, 삭제버튼을 제외한 나머지 공간 모두 채우기 */
  word-break:break-all;
}
.checked{text-decoration: line-through; color: #abd5db;}